<template>
    <ul class="catalog__pagination pagination">
          <li class="pagination__item">
            <a href="#" class="pagination__link pagination__link--arrow" :class="{'pagination__link--disabled': page === prevPage}" @click.prevent="paginate(prevPage)" aria-label="Предыдущая страница">
              <svg width="8" height="14" fill="currentColor">
                <use xlink:href="#icon-arrow-left"></use>
              </svg>
            </a>
          </li>
          <li class="pagination__item" v-for="pageNumber in pages" :key="pageNumber">
            <a href="#" class="pagination__link" :class="{'pagination__link--current': pageNumber === page}" @click.prevent="paginate(pageNumber)">
              {{ pageNumber }}
            </a>
          </li>
          <li class="pagination__item">
            <a class="pagination__link pagination__link--arrow" href="#" :class="{'pagination__link--disabled': page === nextPage}" @click.prevent="paginate(nextPage)" aria-label="Следующая страница">
              <svg width="8" height="14" fill="currentColor">
                <use xlink:href="#icon-arrow-right"></use>
              </svg>
            </a>
          </li>
        </ul>
</template>

<script>
    export default {
        model: {
            prop: 'page',
            event: 'paginate',
        },
        props: ['page', 'count', 'perPage'],
        computed: {
            pages(){
                return Math.ceil(this.count / this.perPage);
            },
            prevPage() {
                if (this.page === 1) {
                  return this.page;
                }
                  return this.page - 1;
            },
            nextPage() {
                if (this.page >= this.pages) {
                  return this.page;
                }
                  return this.page + 1;
            },
        },
        methods: {
            paginate(page){
                this.$emit('paginate', page);
            }
        }
    }
</script>