<template>
    <div class="form__counter">
        <button type="button" aria-label="Убрать один товар"  @click.prevent="deleteAmount">
            <svg width="12" height="12" fill="currentColor">
            <use xlink:href="#icon-minus"></use>
            </svg>
        </button>

        <input type="text" v-model.number="productAmount">

        <button type="button" aria-label="Добавить один товар" @click.prevent="addAmount">
            <svg width="12" height="12" fill="currentColor">
            <use xlink:href="#icon-plus"></use>
            </svg>
        </button>
    </div>
</template>

<script>
export default {
  name: 'ProductAmount',
  props: ['value'],
  data() {
    return {
        productAmount: this.value,
    };
  },
  methods: {
    deleteAmount() {
      if (this.productAmount > 1) {
        this.productAmount--;
        this.$emit('update:value', Math.trunc(this.productAmount));
      }
    },
    addAmount() {
      this.productAmount++;
      this.$emit('update:value', Math.trunc(this.productAmount));
    },
  },
};
</script>