<template>
     
    <li class="catalog__item" :product="product" :key="index ">
            <router-link class="catalog__pic" :to="{name: 'product', params: {id: product.id}}" >
              <img :src="product.image" :alt="product.title">
            </router-link>

            <h3 class="catalog__title">
              <a href="#">
                {{ product.title }}
              </a>
            </h3>

            <span class="catalog__price">
              {{ product.price | numberFormat}} ₽
            </span>

            <ul class="colors colors--black">
              <li class="colors__item" v-for="colorProduct in product.colors.map(colors => colors.code)" :key="colorProduct" :colorProduct="colorProduct">
                <label class="colors__label">
                  <input class="colors__radio sr-only" type="radio" :value="colorProduct" v-model="color">
                  <span class="colors__value" :style="{'background-color': colorProduct}">
                  </span>
                </label>
              </li>
              <!-- <li class="colors__item">
                <label class="colors__label">
                  <input class="colors__radio sr-only" type="radio" value="#8BE000" v-model="color">
                  <span class="colors__value" style="background-color: #8BE000;">
                  </span>
                </label>
              </li>
              <li class="colors__item">
                <label class="colors__label">
                  <input class="colors__radio sr-only" type="radio" value="#222" v-model="color">
                  <span class="colors__value" style="background-color: #222;">
                  </span>
                </label>
              </li> -->
            </ul>
    </li>
    
</template>

<script>
import gotoPage from '@/helpers/gotoPage';
import numberFormat from '@/helpers/numberFormat';

export default {
  data(){
    return {      
      color: this.product.colors.map(colors => colors.code),
    };
  },
  filters: {
            numberFormat
        },
  methods: {
    gotoPage
  },
  name: 'ProductItem',
  props: ['product'], 
}


</script>